@charset "UTF-8";
/*CSS Reset*/
body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
legend,
input,
textarea,
p,
blockquote,
th,
td,
header,
hgroup,
nav,
section,
article,
aside,
footer,
figure,
figcaption,
menu,
button {
  margin: 0;
  padding: 0;
  outline: 0; }

body {
  font-family: 'Microsoft YaHei', 'Verdana', 'SimSun';
  line-height: 1.5;
  font-size: 12px;
  color: #000;
  -webkit-user-select: text;
  -webkit-text-size-adjust: none;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  user-select: text; }

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: normal; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

fieldset,
img {
  border: 0; }

li {
  list-style: none; }

input,
button,
textarea {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  border: 0;
  border-radius: 0;
  background: none;
  -webkit-appearance: none;
  outline: 0; }

select {
  outline: 0;
  border: 0; }

a:link {
  -webkit-touch-callout: none;
  text-decoration: none;
  color: #666; }

a:visited {
  color: #666; }

em, i {
  font-style: normal; }

::-webkit-input-placeholder {
  color: #999; }

